
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Alert from 'vux/src/components/alert/index.vue'

import {
mapState, mapMutations, mapGetters, mapActions
} from "vuex";

import * as types  from '@/store/type';

export default {
name: 'Login',
components: {
ViewBox, XHeader, Box, XInput, XButton, Grid, GridItem,
Group, GroupTitle, Confirm, InlineXNumber, Tabbar, TabbarItem, Alert
},
data(){
return {
name:'',
email:'',
mobile: '',
qq:'',
passwd: '',
passwdConfirm:'',
invitationCode:'',
code: '',
codeVal: '获取验证码',
codeFlog: false,
wait: 60,
city:'',
id_card:'',
}
},
computed: {
...mapGetters([
'cart_goods_list'
])
},
methods: {
signUp() {
const that = this
this.$http.post('http://139.199.168.158:8080/admin/signup_student', {
username:this.name,
password: this.passwd,
phone : this.mobile,
email : this.email,
code : this.invitationCode,
qq:this.qq,
city:this.city,
id_card:this.id_card
}).then((response) => {
console.log(response)
response = response.data;
if (response.code === 1) {
console.log("注册成功")
this.$vux.alert.show({
title: '提示',
content: '注册成功',
onHide () {
that.$router.go(-2)
}
})
} else {
console.log("注册失败")
this.$vux.alert.show({
title: '提示',
content: '注册失败 请联系教务人员',
})







}
});
},


























time() {
if (this.wait === 0) {
this.codeFlog = false;
this.codeVal = "获取验证码";
this.wait = 60;
} else {
this.codeFlog = true;
this.codeVal = "重新发送(" + this.wait + ")";
this.wait--;
setTimeout( () =>{
this.time()
}, 1000);
}
},
}
}



